@import "../utils/vars";
@import "../utils/mixins";

.logo {
  width: 10rem;
}

.header {
  // position: relative;
  width: 100%;
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  z-index: 100;

  background-color: #000;

  @include onTablet {
    box-shadow: 0px 20px 30px #000;
  }

  &--pocketbook {
    position: relative;
    top: 0;
    box-shadow: none;
    background-color: transparent;
  }

  &--none-shadow {
    position: relative;
    box-shadow: none;
  }

  &__portfolio {
    @include onTablet {
      box-shadow: none;
    }
  }

  &__content {  

    // @include onTablet {
    //   box-shadow: 20px 50px 40px #000000;
    // }

    display: flex;
    justify-content: space-between;
    align-items: center;

    // margin-bottom: 1rem;
    padding-top: 18px;

  }

  &__menu-img {
    cursor: pointer;
  }

  &__left-block {
    display: flex;
    align-items: center;
    column-gap: 2rem;
  }

  &__menu {
    &-right {
      display: block;
    
      @include onTablet {
        display: none;
      }
    }

    &-left {
      display: none;

      @include onTablet {
        display: block;
      }

      @include onSmallDesktop {
        display: none;
      }
    }
  }

  &__right-block {
    display: flex;
    align-items: center;
  }

  &__nav-list {
    display: none;

    @include onSmallDesktop {
      display: flex;
      column-gap: 40px;
      margin-right: 50px;
    }
  }

  &__nav-link {
    color: $white-text-color;
    font-family: Fira Code;
    font-size: 1.1rem;
    text-decoration: none;
    font-style: normal;
    font-weight: 600;
    line-height: 30px; /* 150% */
    letter-spacing: 0.4px;
  }

  &__button {
    display: none;

    @include onTablet {
      display: block;
      padding: .6rem 1.5rem;

      border: 2px solid $sub-yellow-text-color;
      background-color: transparent;

      font-family: "Fira Code";
      color: #F7E439;
      text-align: center;
      font-size: 1rem;
      font-style: normal;
      font-weight: 600;
      cursor: pointer;

      &:hover {
        background-color: $sub-yellow-text-color;
        color: $dark-text-color
      }
    }
  }
}

.header-menu {
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  background-color: black;
  height: 100vh;

  transition: transform;
  transition-duration: 300ms;
  transition-timing-function: ease-in-out;
  transform: translateY(-100%);

  overflow-y: auto;

  @include onTablet {
    display: none;
  }

  &--open {
    transition: transform;
    transition-duration: 300ms;
    transition-timing-function: ease-in-out;
    transform: translateY(0);
  }

  &__content {
    padding: 0 10px;
    height: 100%;

    display: flex;
    flex-direction: column;

    &--position {
      margin-bottom: 100px;
    }
  }

  &__list {
    padding: 0;
    display: flex;
    flex-direction: column;
    row-gap: 55px;
    align-items: center;
  
    margin-bottom: 150px;
  }

  &__link {
    color: $white-text-color;
    font-family: $fira-code;
    font-size: 2.5rem;
    font-style: normal;
    font-weight: 500;
    line-height: 30px; /* 107.143% */
    letter-spacing: 0.56px;
    text-decoration: none;
  }
}